<template>
  <div class="particles" ref="canvasWrap">
    <canvas class="canvasBox" ref="canvasBox"></canvas>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: 'particles',
  data () {
    return {
    }
  },
  methods: {
    // 根据传入的config初始化画布
    canvasInit (canvasConfig) {
      canvasConfig = canvasConfig || {}
      let canvasObj = this.$refs.canvasBox
      let canvasWrap = this.$refs.canvasWrap
      let canvas = {
        element: canvasObj,
        points: [],
        // 默认配置
        config: {
          vx: canvasConfig.vx || 4,
          vy: canvasConfig.vy || 4,
          height: canvasConfig.height || 2,
          width: canvasConfig.width || 2,
          count: canvasConfig.count || 200,
          color: canvasConfig.color || '121, 162, 185',
          stroke: canvasConfig.stroke || '130, 255, 255',
          dist: canvasConfig.dist || 6000, // 点吸附距离,
          e_dist: canvasConfig.e_dist || 20000, // 鼠标吸附加速距离
          max_con: 10 // 点到点最大连接数
        }
      }

      // 获取context
      if (canvas.element.getContext('2d')) {
        canvas.context = canvas.element.getContext('2d')
      } else {
        return null
      }

      // 设置canvas大小
      this.canvasSize(canvas.element)
      window.addEventListener('resize', () => {
        this.canvasSize(canvas.element)
      })
      canvasWrap.addEventListener('mousemove', (e) => {
        let event = e || window.event
        canvas.mouse = {
          x: event.clientX,
          y: event.clientY
        }
      })

      // 鼠标离开
      canvasWrap.addEventListener('mouseleave', () => {
        canvas.mouse = undefined
      })

      // 动画帧
      setInterval(() => {
        this.drawPoint(canvas)
      }, 40)
    },
    // 设置canvas大小
    canvasSize (canvas) {
      let width = this.$refs.canvasBox.style.width - 0
      let height = this.$refs.canvasBox.style.height - 0
      canvas.width = width || window.innerWeight || document.documentElement.clientWidth || document.body.clientWidth
      canvas.height = height || window.innerWeight || document.documentElement.clientHeight || document.body.clientHeight
    },
    // 画点
    drawPoint (canvas) {
      let context = canvas.context
      let point
      context.clearRect(0, 0, canvas.element.width, canvas.element.height)
      context.beginPath()
      context.fillStyle = 'rgb(' + canvas.config.color + ')'
      for (let i = 0, len = canvas.config.count; i < len; i++) {
        if (canvas.points.length !== canvas.config.count) {
          // 初始化所有点
          point = {
            x: Math.floor(Math.random() * canvas.element.width),
            y: Math.floor(Math.random() * canvas.element.height),
            vx: canvas.config.vx / 2 - Math.random() * canvas.config.vx,
            vy: canvas.config.vy / 2 - Math.random() * canvas.config.vy
          }
        } else {
          // 处理球的速度和位置, 并且做边界处理
          point = this.borderPoint(canvas.points[i], canvas)
        }
        context.fillRect(point.x - canvas.config.width / 2, point.y - canvas.config.height / 2, canvas.config.width, canvas.config.height)
        canvas.points[i] = point
      }
      this.drawLine(context, canvas, canvas.mouse)
      context.closePath()
    },
    // 边界处理
    borderPoint (point, canvas) {
      let p = point
      if (point.x <= 0 || point.x >= canvas.element.width) {
        p.vx = -p.vx
        p.x += p.vx
      } else if (point.y <= 0 || point.y >= canvas.element.height) {
        p.vy = -p.vy
        p.y += p.vy
      } else {
        p = {
          x: p.x + p.vx,
          y: p.y + p.vy,
          vx: p.vx,
          vy: p.vy
        }
      }
      return p
    },
    // 画线
    drawLine (context, canvas, mouse) {
      let dist
      context = context || canvas.context
      for (let i = 0, len = canvas.config.count; i < len; i++) {
        // 初始化最大连接数
        canvas.points[i].max_con = 0
        // point to point
        for (let j = 0; j < len; j++) {
          if (i !== j) {
            dist = Math.round(canvas.points[i].x - canvas.points[j].x) * Math.round(canvas.points[i].x - canvas.points[j].x) +
              Math.round(canvas.points[i].y - canvas.points[j].y) * Math.round(canvas.points[i].y - canvas.points[j].y)
            // 两点距离小于吸附距离，而且小于最大连接数，则画线
            if (dist <= canvas.config.dist && canvas.points[i].max_con < canvas.config.max_con) {
              canvas.points[i].max_con++
              // 距离越远，线条越细，而且越透明
              context.lineWidth = 0.5 - dist / canvas.config.dist
              context.strokeStyle = 'rgba(' + canvas.config.stroke + ',' + (1 - dist / canvas.config.dist) + ')'
              context.beginPath()
              context.moveTo(canvas.points[i].x, canvas.points[i].y)
              context.lineTo(canvas.points[j].x, canvas.points[j].y)
              context.stroke()
            }
          }
        }
        // 如果鼠标进入画布
        // point to mouse
        if (mouse) {
          dist = Math.round(canvas.points[i].x - mouse.x) * Math.round(canvas.points[i].x - mouse.x) + Math.round(canvas.points[i].y - mouse.y) * Math.round(canvas.points[i].y - mouse.y)
          // 遇到鼠标吸附距离时加速，直接改变point的x，y值达到加速效果
          if (dist > canvas.config.dist && dist <= canvas.config.e_dist) {
            canvas.points[i].x = canvas.points[i].x + (mouse.x - canvas.points[i].x) / 20
            canvas.points[i].y = canvas.points[i].y + (mouse.y - canvas.points[i].y) / 20
          }
          if (dist <= canvas.config.e_dist) {
            context.lineWidth = 1
            context.strokeStyle = 'rgba(' + canvas.config.stroke + ',' + (1 - dist / canvas.config.e_dist) + ')'
            context.beginPath()
            context.moveTo(canvas.points[i].x, canvas.points[i].y)
            context.lineTo(mouse.x, mouse.y)
            context.stroke()
          }
        }
      }
    }
  },
  mounted () {
    this.canvasInit()
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.particles
  position relative
  top 0
  left 0
  width 100%
  height 100%
  background rgb(0, 0, 0)
  .canvasBox
    position absolute
    top 0
    left 0
    z-index 0
</style>



